import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { CourseService } from "./services/course.service";
import swal from "sweetalert2";

@Component({
  selector: "app-course",
  templateUrl: "./course.component.html",
  styleUrls: ["./course.component.scss"]
})
export class CourseComponent implements OnInit {
  courseList;

  constructor(public courseservice: CourseService, private router: Router) {
    this.courseList = [];
  }

  ngOnInit() {
    this.courseservice.getCourse().subscribe(
      (data: any) => {
        console.log("courselist>>", data);
        this.courseList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("course id>>", id);
    this.router.navigate(["course/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the course ?",
        text: "You won't be able to revert the deleted course",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.courseservice.deleteCourse(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Course has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
