import { Component, OnInit } from "@angular/core";

import { CourseService } from "../services/course.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Course } from "app/shared/models/course.model";

@Component({
  selector: "app-edit-course",
  templateUrl: "./edit-course.component.html",
  styleUrls: ["./edit-course.component.scss"]
})
export class EditCourseComponent implements OnInit {
  updatedCourse: Course;
  courseid;

  constructor(
    public courseervice: CourseService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedCourse = {};

    this.courseid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.courseervice.getcourseById(this.courseid).subscribe(
      (data: any) => {
        console.log("courselist>>", data);
        this.updatedCourse = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedCourse);

    swal
      .fire({
        title: "Do you want to edit the course details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.courseervice.editcourse(this.updatedCourse).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Course has been edited.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
