import { Component, OnInit } from "@angular/core";
import { CourseService } from "../services/course.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { Course } from "app/shared/models/course.model";

@Component({
  selector: "app-add-course",
  templateUrl: "./add-course.component.html",
  styleUrls: ["./add-course.component.scss"]
})
export class AddCourseComponent implements OnInit {
  newcourse: Course;
  submitting: boolean;

  constructor(
    public courseervice: CourseService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newcourse = {};
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newcourse);

    swal
      .fire({
        title: "Do you want to add the course ?",
        text: "New course will be added in Course List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.courseervice.addCourse(this.newcourse).subscribe(
            (data: any) => {
              swal.fire("Add!", "Course has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
